<template>
  <div v-if="user" class="user">
    <Panel>
      <template #header> <router-link to="/">主页</router-link> / </template>
      <template #content>
        <div class="content">
          <div class="avator">
            <el-avatar
              shape="square"
              size="medium"
              :src="user.avatar_url"
            ></el-avatar>
            <span class="name">{{ user.loginname }}</span>
          </div>
          <div>{{ user.score }}积分</div>
          <div>
            github<a
              target="_blank"
              :href="`https://github.com/${user.githubUsername}`"
              >@{{ user.githubUsername }}</a
            >
          </div>
          <div>注册时间 {{ user.create_at | moment("from", "now") }}</div>
        </div>
      </template>
    </Panel>
    <Panel>
      <template #header>最近创建的话题 </template>
      <template v-if="user.recent_topics.length" #content>
        <PostListItem
          :list="list"
          v-for="list in recent_topics.slice(0,5)"
          :key="list.id"
        />
        <router-link :to="`/user/${user.loginname}/topics`">查看更多》</router-link>
      </template>
      <template v-else #content> 没有最近创建的话题 </template>
    </Panel>
    <Panel>
      <template #header>最近参与的话题 </template>
      <template v-if="user.recent_replies.length" #content>
        <PostListItem
          :list="list"
          v-for="list in recent_replies"
          :key="list.id"
        />
      </template>
      <template v-else #content> 没有最近参与的话题 </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostListItem from "../components/postListItem.vue";
import Panel from "../containers/Panel.vue";
import axios from '../plugins/axios'
export default {
  components: { Panel, PostListItem },
  data() {
    return {
      recent_replies: [],
      recent_topics:[],
    }
  },
  computed: {
    ...mapState({
      user: (state) => state.user.userInfo,
    }),
  },
  watch: {
    '$route.params.user':{
      handler(newValue){
        this.getUserInfo(newValue)
      },
      immediate:true
    },
    // 当user发生改变的时候 需要重新获取相关的信息
    user:{
      // 监听 user 的变化
      // 1.首次进入 user页，默认user是null 然后action触发之后 user更新了
      async handler(newValue){
        //注意请求需要发多个 而且要等待所有请求结束  才能执行其他操作
        const res1 =  await (await axios.all(newValue.recent_replies.map(topic => axios.get(`/topic/${topic.id}`)  ))).map(ele => ele.data)
        this.recent_replies = res1

        const res2 =  await (await axios.all(newValue.recent_topics.map(topic => axios.get(`/topic/${topic.id}`)  ))).map(ele => ele.data)
        this.recent_topics = res2
        // console.log(res);
      }
    }
  },
  methods: {
    ...mapActions(["getUserInfo"]),
  },
};
</script>

<style lang='less'>
.user {
  .name {
    display: inline-block;
    // height: 36px;
  }
  .content{
    >div{
      margin-bottom: 20px;
    }
    .avator{
      display: flex;
    }
  }
}
</style>